@keyframes key-anim {
  0% {
    left: 35%;
    bottom: 18%;
    transform: rotate(-45deg);
    height: 90px;
  }
  50% {
    left: 50%;
    bottom: 43%;
    transform: rotate(0deg) translateX(-50%);
    height: 200px;
  }
  100% {
    left: 50%;
    bottom: 48%;
    transform: rotate(0deg) translate(-50%, -50%);
    height: 1px;
  }
}

@keyframes key-anim-tabl {
  0% {
    left: 20%;
    bottom: 18%;
    transform: rotate(-45deg);
    height: 70px;
  }
  50% {
    left: 50%;
    bottom: 43%;
    transform: rotate(0deg) translateX(-50%);
    height: 170px;
  }
  100% {
    left: 50%;
    bottom: 50%;
    transform: rotate(0deg) translate(-50%, -50%);
    height: 1px;
  }
}

@keyframes key-anim-mob {
  0% {
    left: 38%;
    bottom: 9%;
    transform: rotate(-45deg);
    height: 70px;
  }
  50% {
    left: 50%;
    bottom: 43%;
    transform: rotate(0deg) translateX(-50%);
    height: 130px;
  }
  100% {
    left: 50%;
    bottom: 54%;
    transform: rotate(0deg) translate(-50%, -50%);
    height: 1px;
  }
}

@keyframes key-dramm {
  from {
    transform: rotate(-49deg);
  }
  to {
    transform: rotate(-35deg);
  }
}

@keyframes text-light {
  from {
    opacity: 0.3;
  }
  to {
    opacity: 0.8;
  }
}

